import { TrackType } from 'react-timelines';

export type TrackDataType = Omit<TrackType, 'sideComponent' | 'hasButton'>;

const LOCAL_STORAGE_KEY = 'timeline-ls';
const db = {
  get: async (): Promise<TrackDataType[]> => {
    return JSON.parse(localStorage.getItem(LOCAL_STORAGE_KEY) || '[]');
  },

  set: async (data: TrackDataType[]) => {
    return localStorage.setItem(LOCAL_STORAGE_KEY, JSON.stringify(data));
  },
};

export default db;
