import styled from 'styled-components';
import { trackIcon } from './style';

type TrackSideComponentProps = {
  onRemove: () => void;
  onCreate: () => void;
};

export const TrackSideComponent = ({
  onRemove,
  onCreate,
}: TrackSideComponentProps) => {
  return (
    <StyledContainer>
      <StyledIcon onClick={onRemove}>x</StyledIcon>
      <StyledIcon onClick={onCreate}>+</StyledIcon>
    </StyledContainer>
  );
};

const StyledContainer = styled.div`
  display: flex;
`;

const StyledIcon = styled.span`
  ${trackIcon}
  width: 40px;
  margin-right: 8px;
  &:last-child {
    margin-right: 0;
  }
`;
