import { useCallback, useEffect, useMemo, useState } from 'react';
import ms from 'ms';
import Timeline, { ElementType, TimeBarType, TrackType } from 'react-timelines';

import { getColorStyle } from './builders';
import db, { TrackDataType } from './db';

import 'react-timelines/lib/css/style.css';
import styled from 'styled-components';
import { trackIcon } from './style';
import { TrackSideComponent } from './TrackSideComponent';
import dayjs from '../../utils/dayjs';
import { round } from './utils';
import { GroupRoundFunctionType } from './helpers';

//#region Constants
const INITIAL_DATE = new Date();
export enum TimebarGroupEnum {
  FIFTEEN_MINUTE,
  HOUR,
  DAY,
  WEEK,
  MONTH,
  THREE_MONTH,
  YEAR,
  FIVE_YEAR,
}

const timebarGroupOrder = Object.keys(TimebarGroupEnum)
  .filter((i) => parseInt(i, 10).toString() === i)
  .map((i) => parseInt(i, 10))
  .sort() as TimebarGroupEnum[];

const MIN_ZOOM = 1;
const MAX_ZOOM = 200;

export const timebarGroupRound: Record<
  TimebarGroupEnum,
  GroupRoundFunctionType
> = {
  [TimebarGroupEnum.FIVE_YEAR]: (date) => ({
    start: new Date(round.downTo(5)(date.getFullYear()).toString()),
    end: new Date((round.downTo(5)(date.getFullYear()) + 5).toString()),
  }),
  [TimebarGroupEnum.YEAR]: (date) => ({
    start: dayjs(date).startOf('year').toDate(),
    end: dayjs(date).startOf('year').add(1, 'year').toDate(),
  }),
  [TimebarGroupEnum.THREE_MONTH]: (date) => ({
    start: dayjs(date).startOf('quarter').toDate(),
    end: dayjs(date).startOf('quarter').add(1, 'quarter').toDate(),
  }),
  [TimebarGroupEnum.MONTH]: (date) => ({
    start: dayjs(date).startOf('month').toDate(),
    end: dayjs(date).startOf('month').add(1, 'month').toDate(),
  }),
  [TimebarGroupEnum.WEEK]: (date) => ({
    start: dayjs(date).startOf('week').toDate(),
    end: dayjs(date).startOf('week').add(1, 'week').toDate(),
  }),
  [TimebarGroupEnum.DAY]: (date) => ({
    start: dayjs(date).startOf('day').toDate(),
    end: dayjs(date).startOf('day').add(1, 'day').toDate(),
  }),
  [TimebarGroupEnum.HOUR]: (date) => ({
    start: dayjs(date).startOf('hour').toDate(),
    end: dayjs(date).startOf('hour').add(1, 'hour').toDate(),
  }),
  [TimebarGroupEnum.FIFTEEN_MINUTE]: (date) => ({
    start: new Date(round.downTo(ms('15 minute'))(date.getTime())),
    end: new Date(
      round.downTo(ms('15 minute'))(
        dayjs(date).add(15, 'minute').toDate().getTime()
      )
    ),
  }),
};

const timebarGroupCellTitle: Record<
  TimebarGroupEnum,
  (start: Date, end: Date) => string
> = {
  [TimebarGroupEnum.FIVE_YEAR]: (start, end) =>
    `${start.getFullYear()} - ${end.getFullYear()}`,
  [TimebarGroupEnum.YEAR]: (start) => `${start.getFullYear()}`,
  [TimebarGroupEnum.THREE_MONTH]: (start) =>
    `Q${Math.ceil((start.getMonth() + 1) / 3)}`,
  [TimebarGroupEnum.MONTH]: (start) => dayjs(start).format('MMM'),
  [TimebarGroupEnum.WEEK]: (start, end) =>
    `${start.getDate()} - ${end.getDate()}`,
  [TimebarGroupEnum.DAY]: (start) => start.getDate().toString(),
  [TimebarGroupEnum.HOUR]: (start) => start.getHours().toString(),
  [TimebarGroupEnum.FIFTEEN_MINUTE]: (start) => start.getMinutes().toString(),
};

const timebarGroupTitle: Record<TimebarGroupEnum, string> = {
  [TimebarGroupEnum.FIVE_YEAR]: 'Five Year',
  [TimebarGroupEnum.YEAR]: 'Year',
  [TimebarGroupEnum.THREE_MONTH]: 'Quarter',
  [TimebarGroupEnum.MONTH]: 'Month',
  [TimebarGroupEnum.WEEK]: 'Week',
  [TimebarGroupEnum.DAY]: 'Day',
  [TimebarGroupEnum.HOUR]: 'Hour',
  [TimebarGroupEnum.FIFTEEN_MINUTE]: 'Fifteen Minute',
};

//#endregion

//#region Utils
export const getTimebarCells = (
  res: { start: Date; end: Date },
  round: GroupRoundFunctionType
): { start: Date; end: Date }[] => {
  const items: { start: Date; end: Date }[] = [];

  let point = res.start;

  while (point < res.end) {
    const roundRes = round(point);
    items.push({ start: roundRes.start, end: roundRes.end });

    point = roundRes.end;
  }

  return items;
};

function getStartAndEndByTracks({ tracks }: { tracks: TrackDataType[] }) {
  let start = new Date();
  let end = new Date();

  const updateByEl = (el: ElementType) => {
    const startDate = new Date(el.start);
    if (startDate < start) {
      start = startDate;
    }

    const endDate = new Date(el.end);
    if (endDate > end) {
      end = endDate;
    }
  };

  tracks.forEach((track) => {
    track.elements.forEach(updateByEl);
    track.tracks?.forEach((subTrack) => subTrack.elements.forEach(updateByEl));
  });

  return { start, end };
}

function getTimeBarsByZoomAndStartEnd({
  parent,
  child,
  start,
  end,
}: {
  parent: TimebarGroupEnum;
  child: TimebarGroupEnum;
  start: Date;
  end: Date;
}): [TimeBarType, TimeBarType] {
  const groupRound = timebarGroupRound[parent];
  const childRound = timebarGroupRound[child];

  const groupRenderer = timebarGroupCellTitle[parent];
  const childRenderer = timebarGroupCellTitle[child];

  const startDate = groupRound(start).start;
  const endDate = groupRound(end).end;

  const getGroups = getTimebarCells(
    { start: startDate, end: endDate },
    groupRound
  ).map((res) => ({
    ...res,
    title: groupRenderer(res.start, res.end),
    cells: getTimebarCells(res, childRound).map((cell) => ({
      ...cell,
      title: childRenderer(cell.start, cell.end),
    })),
  }));

  return [
    {
      id: timebarGroupTitle[parent],
      title: timebarGroupTitle[parent],
      cells: getGroups.map((cell, index) => ({
        id: `${parent}-${index}`,
        title: cell.title,
        start: cell.start,
        end: cell.end,
      })),
    },
    {
      id: timebarGroupTitle[child],
      title: timebarGroupTitle[child],
      cells: getGroups
        .reduce(
          (acc, cur) => {
            acc.push(...cur.cells);
            return acc;
          },
          [] as {
            title: string;
            start: Date;
            end: Date;
          }[]
        )
        .map((cell, index) => ({
          id: `${child}-${index}`,
          title: cell.title,
          start: cell.start,
          end: cell.end,
        })),
    },
  ];
}

//#endregion

type AppProps = {};

export const App = (props: AppProps) => {
  const [isLoaded, setIsLoaded] = useState(false);
  const [tracks, setTracks] = useState<TrackDataType[]>([]);
  const [zoom, setZoom] = useState(10);
  const [isOpen, setIsOpen] = useState(false);
  const [now, setNow] = useState(() => new Date());

  const [parent, setParent] = useState(TimebarGroupEnum.THREE_MONTH);
  const [child, setChild] = useState(TimebarGroupEnum.MONTH);

  const { start, end } = useMemo(
    () => getStartAndEndByTracks({ tracks }),
    [tracks]
  );
  const timeBarItems = useMemo(
    () =>
      isLoaded
        ? getTimeBarsByZoomAndStartEnd({ start, end, parent, child })
        : [],
    [isLoaded, start, end, parent, child]
  );
  const groupCells = timeBarItems[0]?.cells;
  const groupStart = groupCells?.[0]?.start;
  const groupEnd = groupCells?.[groupCells.length - 1]?.end;

  const handleTrackCreate = useCallback(() => {
    const title = prompt('Title:');

    if (title) {
      setTracks((prev) => [
        ...prev,
        { id: Date.now().toString(), title, elements: [] },
      ]);
    }
  }, []);

  const handleTrackRemove = useCallback((id: string) => {
    // eslint-disable-next-line no-restricted-globals
    const shouldRemove = confirm('Do you want to remove?');

    if (shouldRemove) {
      setTracks((prev) => prev.filter((item) => item.id !== id));
    }
  }, []);

  const handleElementCreate = useCallback((parentId: string, subId: string) => {
    const title = prompt('Title:');

    if (!title) {
      return;
    }
    const start = prompt('Start:');

    if (!start) {
      return;
    }

    const end = prompt('End:');

    if (!end) {
      return;
    }

    if (title && start && end) {
      setTracks((prev) =>
        prev.map((item) => {
          if (item.id !== parentId) {
            return item;
          }

          return {
            ...item,
            tracks: (item.tracks || []).map((track) => {
              if (track.id !== subId) {
                return track;
              }

              return {
                ...track,
                elements: [
                  ...track.elements,
                  {
                    id: Date.now().toString(),
                    trackId: item.id,
                    title,
                    start: new Date(start),
                    end: new Date(end),
                  },
                ],
              };
            }),
          };
        })
      );
    }
  }, []);

  const handleSubTrackCreate = useCallback((id: string) => {
    const title = prompt('Title:');

    if (title) {
      setTracks((prev) =>
        prev.map((track) =>
          track.id !== id
            ? track
            : {
                ...track,
                tracks: [
                  ...(track.tracks || []),
                  {
                    id: Date.now().toString(),
                    elements: [],
                    title,
                  },
                ],
              }
        )
      );
    }
  }, []);

  const tracksData = useMemo<TrackType[]>(() => {
    const addRow: TrackType = {
      id: 'add-row',
      elements: [],
      title: 'Add Track',
      sideComponent: (
        <StyledAddIcon onClick={handleTrackCreate}>+</StyledAddIcon>
      ),
    };

    return [
      ...tracks.map((track) => ({
        ...track,
        isOpen: Boolean(track.isOpen),
        sideComponent: (
          <TrackSideComponent
            onRemove={() => handleTrackRemove(track.id)}
            onCreate={() => handleSubTrackCreate(track.id)}
          />
        ),
        tracks: (track.tracks || []).map((subTrack) => ({
          ...subTrack,
          elements: subTrack.elements.map((el) => ({
            ...el,
            start: new Date(el.start),
            end: new Date(el.end),
            style: {
              ...getColorStyle(),
              borderRadius: '4px',
              boxShadow: '1px 1px 0px rgba(0, 0, 0, 0.25)',
            },
          })),

          sideComponent: (
            <TrackSideComponent
              onRemove={() => handleTrackRemove(subTrack.id)}
              onCreate={() => handleElementCreate(track.id, subTrack.id)}
            />
          ),
        })),
      })),
      addRow,
    ];
  }, [
    handleTrackCreate,
    tracks,
    handleTrackRemove,
    handleSubTrackCreate,
    handleElementCreate,
  ]);

  const toggleOpen = () => setIsOpen((prev) => !prev);

  const handleZoomIn = () => setZoom((prev) => Math.min(prev + 1, MAX_ZOOM));
  const handleZoomOut = () => setZoom((prev) => Math.max(prev - 1, MIN_ZOOM));

  const clickElement = (element: ElementType) => {
    // eslint-disable-next-line no-restricted-globals
    const shouldRemove = confirm('Do you want to remove?');
    if (shouldRemove) {
      setTracks((prev) =>
        prev.map((item) =>
          item.id === element.trackId
            ? {
                ...item,
                elements: item.elements.filter((el) => el.id === element.id),
              }
            : item
        )
      );
    }
  };

  const handleToggleTrackOpen = (track: TrackType) => {
    setTracks((prev) =>
      prev.map((item) =>
        item.id !== track.id
          ? item
          : {
              ...item,
              isOpen: !item.isOpen,
            }
      )
    );
  };

  useEffect(() => {
    const interval = setInterval(() => {
      setNow(new Date());
    }, 1000);

    return () => {
      clearInterval(interval);
    };
  }, []);

  useEffect(() => {
    db.get()
      .then(setTracks)
      .then(() => setIsLoaded(true));
  }, []);

  useEffect(() => {
    if (isLoaded) {
      db.set(tracks);
    }
  }, [tracks, isLoaded]);

  if (!isLoaded || !groupCells) {
    return null;
  }

  return (
    <div>
      <input
        style={{ width: '100%' }}
        type="range"
        min={MIN_ZOOM}
        max={MAX_ZOOM}
        step="1"
        onChange={(e) => setZoom(parseInt(e.target.value, 10))}
      />
      <div style={{ display: 'flex' }}>
        <span>Zoom {zoom}</span>
        &nbsp;
        <label>
          Parent &nbsp;
          <select
            value={parent}
            onChange={(e) => setParent(e.target.value as any)}
          >
            {timebarGroupOrder.map((g) => (
              <option value={g} key={g}>
                {timebarGroupTitle[g]}
              </option>
            ))}
          </select>
        </label>
        &nbsp;
        <label>
          Child &nbsp;
          <select
            value={child}
            onChange={(e) => setChild(e.target.value as any)}
          >
            {timebarGroupOrder.map((g) => (
              <option value={g} key={g}>
                {timebarGroupTitle[g]}
              </option>
            ))}
          </select>
        </label>
        &nbsp;
        <input value={dayjs(INITIAL_DATE).format('MM/DD/YYYY hh:mm A')} />
      </div>

      <Timeline
        scale={{
          start: groupStart,
          end: groupEnd,
          zoom,
          zoomMin: MIN_ZOOM,
          zoomMax: MAX_ZOOM,
        }}
        isOpen={isOpen}
        toggleOpen={toggleOpen}
        zoomIn={handleZoomIn}
        zoomOut={handleZoomOut}
        clickElement={clickElement}
        timebar={timeBarItems}
        tracks={tracksData}
        now={now}
        toggleTrackOpen={handleToggleTrackOpen}
        enableSticky
        scrollToNow
      />
    </div>
  );
};

export default App;

const StyledAddIcon = styled.span`
  ${trackIcon}
`;
